.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 50px 0;
  }
  .card{
      margin: 0 2% !important;
  }
  
  .infected {
    border-bottom: 10px solid rgba(0, 0, 255, 0.5);
  }
  
  .recovered{
    border-bottom: 10px solid rgba(51, 255, 0, 0.637);
  }
  
  .deaths{
    border-bottom: 10px solid rgba(255, 12, 12, 0.856);
  }